@import "sass/index";

.wrapper {
  padding-top: 0;
}

.tableWrapper {
  padding-top: spacing(4);
}

.tableContent {
  font-family: "Gilroy", sans-serif;
  font-weight: bold;
  text-align: center;
  line-height: 15px;
  color: use(--template-statisticsBlock-tab-tableBody);

  @media (max-width: $mobileScreen) {
    font-size: 0.75rem;
  }
}

.profit {
  color: use(--subsidiary-green) !important;
}
.profitLose {
  color: use(--subsidiary-red) !important;
}

.coinWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.coinIcon {
  position: static;
  height: 16px;
  width: 16px;
  cursor: default;
  margin-right: 5px;
}

.allButtonWrapper {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.td {
  @media (max-width: $mobileScreen) {
    padding: 20px 0;
  }
}
